
html {
  height: 100%;
}
body {
  height: 100%;
  background: url(../../static/img/background.296026d.png) no-repeat;
  background-size: 100% 100%
}
#app {
  color: #2c3e50;
  width: 600px;
  font-family: Source Sans Pro, Helvetica, sans-serif;
  text-align: center;
  position: absolute;
  top: 35%;
  left: 50%;
  margin-left: -300px;
  margin-top: -100px;
}

.search-input {
  height: 45px;
  width: 600px;
  margin: 0 auto;
  margin-top: 10px;
  position: relative;
}
.search-input input {
  border: 1px solid #e4e4e4;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 500px;
  height: 45px;
  font-size: 18px;
  float: left;
  padding-left: 10px;
  padding-right: 10px;
  overflow: hidden;
}
.search-btn {
  height: 45px;
  width: 100px;
  border: 1px solid mediumseagreen;
  background-color: mediumseagreen;
  color: white;
  font-size: 16px;
  font-weight: bold;
  float: left;
}
.search-btn {
  cursor: pointer
}
.search-select {
  position: absolute;
  top: 45px;
  width: 500px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  z-index: 999;
}
.search-select li {
  border: 1px solid #d4d4d4;
;
  border-top: none;
  border-bottom: none;
  background-color: #fff;
  width: 100%
}
.search-select-option {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 7px 10px;
}
.selectback {
  background-color: #eee !important;
  cursor: pointer
}
input::-ms-clear {
  display: none
}
.search-reset {
  width: 21px;
  height: 21px;
  position: absolute;
  display: block;
  line-height: 21px;
  text-align: center;
  cursor: pointer;
  font-size: 20px;
  right: 110px;
  top: 12px
}
.search-select-list {
  -webkit-transition: all 0.5s;
  transition: all 0.5s
}
.itemfade-enter,
.itemfade-leave-active {
  opacity: 0;
}
.itemfade-leave-active {
  position: absolute;
}
.selectBack {
  background-color: #eee !important;
  cursor: pointer
}
.search-select ul{margin:0;text-align: left;
}

ul{list-style: none;padding: 0;margin: 0
}
.main-logo {
  width: 600px;
  height: 140px;
  position: relative;
}
.main-logo img {
  display: block;
  margin: 0 auto;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: pointer;
}
.logoList-arrow {
  position: absolute;
  width: 0;
  height: 0;
  border: 8px solid;
  border-color: #000 transparent transparent transparent;
  right: 100px;
  top: 66px;
  cursor: pointer
}
.logoList {
  position: absolute;
  top: 100%;
  width: 200px;
  left: 50%;
  margin-left: -100px;
  z-index: 999999;
  border: 1px solid #d4d4d4
}
.logoList li {
  width: 100%;
  height: 80px;
  background-color: #FEFEFE; /*少个封号报错*/
  line-height: 80px;
  padding-top: 1px;
}
.logoList li img {
  width: 100%;
  margin-top: 10px;
}
.logoFade-enter-active,
.logoFade-leave-active {
  -webkit-transition: all  .5s;
  transition: all  .5s;
}
.logoFade-enter,
.logoFade-leave-active {
  opacity: 0;
  -webkit-transform: translateY(20px);
          transform: translateY(20px);
}
.selectBack{background-color: #eee !important;cursor: pointer
}
